body {
  font-family: sans-serif;
}
#app {
  position: relative;
}

#ball {
  width: 20px;
  height: 20px;
  background-color: red;
  position: fixed;
  top: 20px;
  left: 52.5%;
  border-radius: 50%;
  animation: changeColorBall 1s infinite alternate;
}

@keyframes changeColorBall {
  0% {
    background-color: rgb(91, 16, 16);
  }
  50% {
    background-color: rgb(247, 62, 62);
  }
  100% {
    background-color: rgb(165, 11, 11);
  }
}

#rod1,
#rod2 {
  width: 200px;
  height: 20px;
  position: fixed;
  left: 50%;
  border-radius: 25px;
  text-align: center;
  color: white;
  font-weight: bold;
}

#rod1 {
  background-color: blue;
  top: 0px;
}

#rod2 {
  background-color: blueviolet;
  bottom: 0px;
}
